import { RootState } from './redux-store'
import { ThunkDispatch } from "redux-thunk"
import { Reducer } from 'redux'

import { getArrayAllPopulation } from '../api/api'
import { PersonType } from '../types/types'

export type PopulationStateType = {
    data: PersonType[] | undefined
    error: Error | undefined
    loading: boolean
    alert: boolean
} 

type PropertyiesType<T> = T extends { [key: string]: infer U } ? U : never
type InferActionsType<T extends {[key: string]: (...args: any[]) => any}> = ReturnType<PropertyiesType<T>>
type ActionsType = InferActionsType<typeof actions>

const initialState: PopulationStateType = 
{   
  data: undefined,
  error: undefined,
  loading: false,
  alert: false
}

const populationReducer: Reducer<PopulationStateType, ActionsType> = (state = initialState, action) => { 
    switch (action.type){
        case 'SET_DATA':
            return {
              ...state,
              data: action.arrayPopulation,
              error: undefined,
              loading: false
            }
        case 'SET_ERROR':
            return {
              ...state,
              data: undefined,
              error : action.error,
              loading: false
            }   
        case 'SET_LOADING':
            return {
              ...state,
              data: undefined,
              error: undefined,
              loading: action.loading
            } 
        case 'SET_ALERT':
            return {
              ...state,
              alert: action.alert
            }    
        default:
            return state    
    }
}

export const actions = {
  setDataActionCreator: (arrayPopulation: PersonType[]) => ({type: 'SET_DATA' as const, arrayPopulation}),
  setErrorActionCreator: (error: Error) => ({type: 'SET_ERROR' as const, error}),
  setLoadingActionCreator: (loading: boolean) => ({type: 'SET_LOADING' as const, loading}),
  setAlertActionCreator: (alert: boolean) => ({type: 'SET_ALERT' as const, alert})
}

export const getDataThunk = () => (dispatch: ThunkDispatch<RootState, void, ActionsType>) => {
    dispatch(actions.setLoadingActionCreator(true))
    // setTimeout(() => {
      getArrayAllPopulation()
      .then(response => response.data)   
      // .then(data => dispatch(actions.setDataActionCreator(data.Brastlewark.slice(0, 10))))
      .then(data => dispatch(actions.setDataActionCreator(data.Brastlewark)))
      .catch(error => dispatch(actions.setErrorActionCreator(error)))
    // }, 1500) 
}

export default populationReducer

